@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&family=Fraunces:ital,wght@0,500;0,600;0,700;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*:before,*::after{margin:0;padding:0;box-sizing:border-box}.app{max-width:1900px;width:100%;margin:0 auto;background-color:#f8f3eb;font-family:"DM Sans", sans-serif}.app header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin:0 auto;background-color:#212121;padding:20px 30px;position:-webkit-sticky;position:sticky;top:0px;box-shadow:0 0 20px rgba(0,0,0,0.3);border-bottom:1px solid rgba(15,15,15,0.1)}.app header.scrolled{box-shadow:0 0 20px rgba(0,0,0,0.5);border-bottom:none}.app header .MuiSvgIcon-root{color:white}.app main{padding:30px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}@media (max-width: 540px){.app main{padding:0}}.app main .headlineLines{font-size:1.2em;margin:10px 0}

.card{width:100%;padding:25px;margin:20px 0;text-decoration:none}@media (max-width: 540px){.card{padding:20px;margin:10px 0}}.card:hover{transition:box-shadow 300ms 0ms cubic-bezier(0.4, 0, 0.2, 1);box-shadow:0px 0px 1px 0 rgba(0,0,0,0.6)}.card img{width:100%;border:1px solid rgba(63,61,61,0.5)}.card .title{color:#212121;font-size:1.3em;margin:10px 0px}.card .about{color:#424242;font-size:1.1em;margin:10px 0px;line-height:28px}

.section{width:100%}.section .sectionHeading{font-family:"Fraunces", serif;font-weight:700;line-height:1.25;color:#000;font-size:1.7em;margin-left:40px}@media (max-width: 540px){.section .sectionHeading{margin:20px 20px 0 30px;font-size:1.5em}}.section .cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 3fr));grid-gap:20px;width:100%;padding:20px}@media (max-width: 540px){.section .cards{padding:20px 10px}}

.landingPage{background-color:#212121;height:100vh}@media (max-width: 540px){.landingPage{height:90vh}}


